<script>
import SideBar from "@/views/Side-Bar";
import VanillaTilt from "vanilla-tilt";
import BgColor from "@/components/Bg-Color";
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  components: {BgColor, SideBar, PulseLoader},
  data() {
    return {
      color: [
        '#581b98',
        '#fc5185',
        '#3d6cb9',
        '#ff5959',
        '#492540',

      ]
    }
  },
  methods: {
    Run(color) {
      console.log(`This is app ${color}`);
      let body = document.querySelector("body")
      body.style.background = color;
      body.classList.add("changeColor");


    }
  },

  mounted() {


    window.addEventListener("resize", () => {
      let width = window.innerWidth;
      let height = window.innerHeight;
      if (width < 1200 || height < 700) {
        alert("ဖုန်းတွင်အသုံးပြုလို့အဆင်မပြေပါ");

      } else {
        return false;
      }
    })
    window.addEventListener("load", () => {
      setTimeout(() => {
        document.querySelector(".loader").remove();

      }, 2000)
      let width = window.innerWidth;
      let height = window.innerHeight;
      if (width < 1200 || height < 700) {
        alert("ဖုန်းတွင်အသုံးပြုလို့အဆင်မပြေပါ");

      } else {
        return false;
      }
    })

    VanillaTilt.init(document.querySelector(".side-bar")), {
      max: 25,
      speed: 200,
      glare: true,
      "max-glare": 7,
      reverse: true


    }
    let body = document.querySelector("body")
    body.style.background = "#581b98";
  },
  computed: {
    change() {
      return this.data;
    }
  },

}
</script>