<template>
  <div>

    <div class="size my-2" @click="getColor" :style="{background: color}">

    </div>
  </div>
</template>

<script>
export default {
  name: "Bg-Color",
  props: ['color'],
  methods: {
    getColor() {
      this.$emit("toApp", `${this.color}`)
      // console.log(this.color)
    }
  },


}
</script>

<style scoped>
.size {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  box-shadow: 0 0 0 0.3rem rgba(255, 255, 255, .2);

}

.size:hover {
  cursor: pointer;
}
</style>